.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo-link {
  line-height: 0;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  text-transform: uppercase;
  white-space: nowrap;
  height: 60px;
  margin-left: 20px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  transition-duration: .3s;
  display: flex;
}

.nav__link:link, .nav__link:visited {
  color: #060b35;
  text-decoration: none;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  margin: 0;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}

.is-active:link, .is-active:visited {
  color: #00acdc;
}

.stars {
  display: flex;
}

.stars:last-of-type {
  padding-bottom: 0;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--1 :first-child, .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.card {
  box-sizing: border-box;
  background-image: url("imac.202c52ff.jpeg");
  background-position: 19px 32px;
  background-repeat: no-repeat;
  background-size: 160px 134px;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  transition-duration: .3s;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.card__title {
  text-align: left;
  color: #060b35;
  margin-top: 206px;
  margin-bottom: 4px;
  padding-inline: 16px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__product-code {
  text-align: left;
  color: #616070;
  margin: inherit;
  margin-bottom: 16px;
  padding-inline: 16px;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__reviews {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  padding-inline: 16px;
  display: flex;
}

.card__reviews-counter {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
}

.card__price {
  justify-content: space-between;
  margin-bottom: 16px;
  padding-inline: 16px;
  line-height: 18px;
  display: flex;
}

.card__price-text {
  color: #616070;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
}

.card__price-amount {
  color: #060b35;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  text-align: center;
  color: #fff;
  text-transform: uppercase;
  background: #00acdc;
  border-radius: 5px;
  height: 40px;
  margin: 0 16px 16px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
  transition-duration: .3s;
  display: block;
}

.card__button:hover {
  color: #00acdc;
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #00acdc;
}

.catalog {
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}

body {
  margin: 0;
}
